@import "../../vars.css";

.container {
    position: relative;
    padding-top: 74px;
    padding-left: 74px;
    padding-right: 74px;
    margin-left: 10%;
    margin-right: 10%;
    z-index: 1;
}

.title {
    color: var(--color-text);
    font-size: 35px;
    font-weight: 700;
    letter-spacing: 1.5px;
    text-transform: uppercase;
}

.content {
    display: flex;
    flex-direction: column;
    color: var(--color-text);
    margin-top: 36px;
    list-style: none;
    flex-wrap: wrap;
}

.educationItem {
    display: flex;
    width: 100%;
    flex-direction: column;
    border-radius: 20px;
    padding: 24px;
    margin-bottom: 16px;
    background: linear-gradient(
      90deg, 
      rgba(12, 12, 12, 0.6) 0%, 
      rgba(12, 12, 12, 0.1) 100%
    );
}

.educationItem h3 {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 8px;
}

.institution {
    font-size: 20px;
    margin-top: 16px;
}

.date {
    font-size: 16px;
    margin-top: 16px;
}

@media screen and (max-width: 830px) {
    .container {
        padding-right: 0px;
        padding-left: 0px;
    }
}